/* Account.css */
.login-message {
  transition: opacity 0.5s ease;
}

.login-message.success {
  animation: fadeInOut 1s ease-out;
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}
